.container {
    display: flex;

    align-items: center;
    justify-content: center;

    width: 100%;
    height: 100%;
}

.content {
    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 100%;
}

.microscope-container {
    position: relative;

    width: 490px;
    height: 490px;
}

.microscope-wrap {
    display: flex;
    align-items: flex-end;
}

.degrees {
    font-family: sans-serif;
    font-size: 1.5rem;

    width: 2rem;

    margin-left: 4rem;
    margin-right: auto;
}

@media (min-width: 1048px) {
    .content {
        flex-direction: row;
        align-items: flex-end;
    }
}
